/* Garnett Container */
.login {
  position: absolute;
  background-color: var(--secondary-dark);
  color: #fff;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
}

.tt-logo {
  position: absolute;
  top: 20px;
  left: 20px;
}

/* Logo and Title */
.login-logo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 400px;
  margin: auto;
  margin-top: 75px;
  margin-bottom: 50px;
}
@media(max-width: 768px) {
  .login-logo {
    width: 280px;
    margin-bottom: 20px;
  }
}

.login-logo img {
  height: 75px;
  margin: auto 0;
}
@media(max-width: 768px) {
  .login-logo img {
    height: 50px;
  }
}

.login-logo h1 {
  font-size: 60px;
  margin-bottom: 20px;
  text-transform: uppercase;
}
@media(max-width: 768px) {
  .login-logo h1 {
    font-size: 45px;
  }
}

.tt {
  color: var(--primary-color);
}

/* Sign In and Sign Up Toggle Buttons */
.sign-options {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 400px;
  font-size: 18px;
  margin: auto;
}
@media(max-width: 768px) {
  .sign-options {
    width: 300px;
  }
}

.sign-options span {
    cursor: pointer;
}

.sign-options span:after {
  content: '';
  display: block;
  margin: auto;
  margin-top: 5px;
  height: 2px;
  width: 0;
  background: #960018;
  -webkit-transition: width .5s ease, background-color .5s ease;
  -o-transition: width .5s ease, background-color .5s ease;
  transition: width .5s ease, background-color .5s ease;
}
.sign-options span.underline:after {
  width: 100%;
}

/* Form Fields */
.login-form {
  display: none;
  margin: 20px auto;
}

.login-form.active {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-animation: fadeIn .5s ease-in forwards;
          animation: fadeIn .5s ease-in forwards;
}

.login-input {
  width: 40% !important;
  max-width: 576px;
  margin: auto;
  text-align: left;
}
@media(max-width: 768px) {
  .login-input {
    width: 80% !important;
  }
}

/* Login and Sign Up Buttons */
.login-button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 40px;
  width: 40%;
  max-width: 576px;
  background-color: var(--primary-color);
  color: #fff;
  font-size: 16px;
  margin: auto;
  border-radius: 20px;
  text-transform: uppercase;
  -webkit-box-shadow: rgba(0, 0, 0, 0.16) 0px 8px 10px, rgba(0, 0, 0, 0.23) 0px 8px 10px;
          box-shadow: rgba(0, 0, 0, 0.16) 0px 8px 10px, rgba(0, 0, 0, 0.23) 0px 8px 10px;
  cursor: pointer;
}
@media(max-width: 768px) {
  .login-button {
    width: 80%;
  }
}
.login-button:hover {
  background-color: var(--primary-dark);
}

.login-button:last-child {
  margin-top: 40px;
}

/* Checkbox */
.checkbox-container label {
  position: relative;
  font-weight: 400;
  margin-bottom: 10px;
}
.checkbox-container span::before,
.checkbox-container span::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  cursor: pointer;
}

.login input[type="checkbox"]{
  display: none;
}

.checkbox-container span.checkbox::before {
  width: 20px;
  height: 20px;
  background-color: #fff;
  left: -35px;
}

.checkbox-container span.checkbox::after {
  content: '\e806';
  font-family: 'fontello';
  font-size: 20px;
  left: -35px;
  top: -4px;
  color: transparent;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
}

input[type="checkbox"]:not(:checked) + label span.checkbox:after {
  opacity: 0;
  -webkit-transform : scale(0);
      -ms-transform : scale(0);
          transform : scale(0);
}

input[type="checkbox"]:checked + label span.checkbox:after {
  color: #960018;
  opacity: 1;
  -webkit-transform : scale(1);
      -ms-transform : scale(1);
          transform : scale(1);
}



